export default async function fetchWeatherData(location) {
  const url = `https://weather.visualcrossing.com/VisualCrossingWebServices/rest/services/timeline/${location}?unitGroup=metric&include=hours,current&key=QXAG6D4FT9AJP8N2CCYZ2XED2&contentType=json`;

  try {
    const response = await fetch(url);
    const data = await response.json();
    return data;
  } catch (error) {
    return console.error("Error fetching weather data:", error);
  }
}
