const iconsContext = require.context(
  '../assets/icons/weather-icons', // relative to this file
  false,                           // don't include subdirectories
  /\.svg$/                         // only .svg files
);

const icons = iconsContext.keys().reduce((acc, path) => {
  const iconName = path.replace('./', '').replace('.svg', '');
  acc[iconName] = iconsContext(path);
  return acc;
}, {});



export  {icons}
