import photo from "../assets/images/profile-picture.png";

const infoIcon = document.querySelector("#info-icon");
const popup = document.querySelector("#profile-popup");
const profilePicture = document.querySelector("#profile-picture");
const emailIcon = document.querySelector("#email-icon");
const linkCopiedText = document.querySelector("#link-copied");

let isShown = false;
const email = "adnane102464@gmail.com"; 

export default function showInfo() {
  profilePicture.src = photo;

  infoIcon.addEventListener("click", () => {
    if (!isShown) {
      isShown = true;
      popup.style.display = "block";
      setTimeout(() => {
        popup.style.display = "none";
        isShown = false;
      }, 5000);
    } else {
      isShown = false;
      popup.style.display = "none";
    }
  });

  emailIcon.addEventListener("click", () => {
    navigator.clipboard.writeText(email).then(() => {
      linkCopiedText.style.display = "block";

      setTimeout(() => {
        linkCopiedText.style.display = "none";
      }, 3500);
    }).catch((err) => {
      console.error("Failed to copy email:", err);
    });
  });
}
