import create from "./domCreator";
import { icons } from "./icons";

const cardsContainer = document.querySelector("#cards-container");

function displayWeatherCards(dataObj, weekModeActivated) {
  const hours = ["06:00:00", "09:00:00", "12:00:00", "15:00:00", "18:00:00", "21:00:00"];
  clearCards();

  const fragment = document.createDocumentFragment();

  if (weekModeActivated) {
    for (let i = 0; i < 7; i++) {
      const day = dataObj.days[i];
      if (!day) continue;

      fragment.appendChild(
        createWeatherCard({
          title: new Date(day.datetime).toLocaleDateString("en-US", { weekday: "long" }),
          iconSrc: icons[day.icon] || "", // fallback empty icon
          maxTemp: day.tempmax,
          minTemp: day.tempmin,
          cardClass: "week-card",
          titleClass: "day-name",
        })
      );
    }
  } else {
    const hourlyData = dataObj.days[0];
    if (!hourlyData || !Array.isArray(hourlyData.hours)) return;

    hours.forEach((time) => {
      const hourData = hourlyData.hours.find((hour) => hour.datetime === time);
      if (!hourData) return;

      fragment.appendChild(
        createWeatherCard({
          title: time.substring(0, 5),
          iconSrc: icons[hourData.icon] || "",
          maxTemp: hourData.temp,
          minTemp: null,
          windspeed: hourData.windspeed,
          cardClass: "hourly-card",
          titleClass: "hour-name",
        })
      );
    });
  }

  cardsContainer.appendChild(fragment);
}


function createWeatherCard({
  title,
  iconSrc,
  maxTemp,
  minTemp,
  windspeed,
  cardClass,
  titleClass,
}) {
  const card = create.elWithClass("div", "", cardClass);
  const heading = create.elWithClass("h3", title, titleClass);
  const icon = create.elWithClass("img", "", "weather-icon-card");
  const tempBox = create.elWithClass("div", "", "temp-min-max");

  const max = create.elWithClass("p", `${maxTemp}°C`, "max-temp");
  const secondValue =
    cardClass === "hourly-card"
      ? create.elWithClass("p", `${windspeed} km/h`, "wind-speed")
      : create.elWithClass("p", `${minTemp}°C`, "min-temp");

  icon.src = iconSrc;
  tempBox.append(max, secondValue);
  card.append(heading, icon, tempBox);

  return card;
}


function clearCards() {
  cardsContainer.innerHTML = "";
}

export { displayWeatherCards };
