const region = document.querySelector("#region");
const weatherDescription = document.querySelector("#weather-description");
const temperature = document.querySelector("#temperature");
const dateTime = document.querySelector("#datetime");
const weatherIcon = document.querySelector("#weather-icon");
const valueWind = document.querySelector("#value-wind");
const valueUv = document.querySelector("#value-uv");
const valueHumidity = document.querySelector("#value-humidity");
const valueVisibility = document.querySelector("#value-visibility");
const valueFeelsLike = document.querySelector("#value-feels-like");

export default function displayWeather({
  location,
  weatherDesc,
  temp,
  date,
  iconSrc,
  windValue,
  uvValue,
  humidityValue,
  visibilityValue,
  feelsLikeValue,
}) {
  region.textContent = location ?? "Unknown location";
  weatherDescription.textContent = weatherDesc ?? "No description";
  temperature.textContent = temp ?? "--°C";
  dateTime.textContent = date ?? "--";
  weatherIcon.src = iconSrc ?? "";
  weatherIcon.alt = weatherDesc ?? "Weather icon";

  valueWind.textContent = windValue ?? "-- km/h";
  valueUv.textContent = uvValue ?? "--";
  valueHumidity.textContent = humidityValue ?? "--%";
  valueVisibility.textContent = visibilityValue ?? "-- km";
  valueFeelsLike.textContent = feelsLikeValue ?? "--°C";
}
